/* CSS reset code */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  min-height: 100vh;
}

ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
:root {
  --primary-color: rgb(97, 165, 194);
  --secondary-color: rgb(1, 79, 134);
  --color-accent: #e2eafc;
}

.main-text-1 {
  color: var(--primary-color);
}
.main-text-2 {
  color: var(--secondary-color);
}
h1 {
  font-size: 4rem;
}
h5 {
  color: var(--primary-color);
  font-size: 1.5rem;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-family: "Dosis", sans-serif;
  hyphens: manual;
}

/* navbar */

main {
  margin-top: -50px;
  /* background-color: darkblue; */
}
.navbar ul li {
  margin: 0 30px;
}
.fa-bars,
.fa-times {
  color: var(--secondary-color);
}
.navbar ul li ul {
  float: left;
  border-radius: 10px;
  text-align: left;

  /* margin: 10px; */
  border: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 8px;
}
.navbar li:hover > ul {
  margin-top: 0;
  display: block;
  animation-name: example;
  animation-duration: 1s;
}

.navbar li ul a {
  transform: translateX(-30px);

  margin: 5px;
}
.navbar li ul a:hover {
  background-color: white;
  color: var(--primary-color);
}
.navbar li a {
  color: var(--secondary-color);
}

.content {
  margin-top: 7rem;
}
@keyframes example {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(500px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-250px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
img,
.fadein {
  animation-name: example;
  animation-duration: 2s;
}
.fadeinleft {
  animation-name: fadeInLeft;
  animation-duration: 2s;
}
.fadeinright {
  animation-name: fadeInRight;
  animation-duration: 2s;
}
/* cards */

section:first-child {
  margin-bottom: 100px;
}
section .card {
  align-items: center;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 8px;
  border: none;
  border-radius: 10px;
}
section .card a {
  color: var(--secondary-color) !important;
  text-decoration: none;
}
section:nth-child(2) .card:hover {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 8px;
  border: 0.01px dashed rgba(0, 0, 0, 0.1) !important;
  scale: 1.1;
}
section .card img {
  margin-top: 25px;
  height: 70px;
  width: 70px;
  text-align: center;
}

.card-footer {
  color: var(--secondary-color);
  border-top: 0 !important;
}

/* about us css */

.about-main {
  margin-top: 20px;
}

/* section div .col h2 {
  font-size: 2.2rem;
} */

section div .about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* offers css */
.offers .card {
  border: none;
  outline: none;
}
.offers .card:hover {
  scale: 1.1;
}
.offers div .card img {
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 8px;
  /* background-color: darkgoldenrod; */
}

/* newsletter */

section:nth-child(5) {
  margin-top: 100px;
}
.newsletter {
  background-color: var(--color-accent);
  border-radius: 10px;
}
.newsemail {
  width: 100%;
  color: var(--primary-color);
  height: 50px;
  border: none;
  padding-left: 20px;
  border-radius: 10px;
}

.newsletter div:nth-child(3) button {
  border-radius: 4px;
  display: flex;
  align-items: center;
  height: 35%;
}
.newsletter div:nth-child(2) span {
  font-size: 2vw !important;
  margin: 3px;
}
/* btn css */

.simdi-gonder {
  margin-bottom: 20px !important;
  font-size: 0.9rem !important;
}
.btn.btn-danger {
  font-family: "Roboto", sans-serif;
  background-color: var(--secondary-color);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 15px 30px;
  transition: all 0.3s ease;
  color: white;
  font-size: 1rem;
}
.btn.btn-danger:hover {
  background-color: var(--primary-color);
}

.btn.btn-primary {
  font-family: "Roboto", sans-serif;
  background-color: var(--primary-color);
  border: none;
  border-radius: 10px;
  outline: none;
  transition: all 0.3s ease;
  padding: 15px 30px;
  color: white;
  font-size: 16px;
}
.btn.btn-primary:hover {
  background-color: var(--secondary-color);
}

.reveal.active {
  transform: translateX(0);
  animation-name: example;
  animation-duration: 500ms;
  opacity: 1;
}

/* prices */

section:nth-child(6) {
  margin-top: 100px;
}
section:nth-child(7) img {
  width: 25%;
  height: 25%;
}

section:nth-child(7) .card:hover {
  scale: 1.04;
}
section:nth-child(7) p {
  font-family: "Dosis", sans-serif;
  margin: 30px auto;
}
section:nth-child(7) p .main-text-2 {
  /* background-color: turquoise; */
  font-weight: 800;
  font-size: 3rem;
}
section:nth-child(7) p .main-text-1 {
  /* background-color: turquoise; */
  font-weight: 800;
  font-size: 1rem;
}
section:nth-child(7) p:nth-child(3) {
  /* background-color: turquoise; */
  font-family: "Roboto", sans-serif;
  color: var(--primary-color);
  margin-top: 0px;
  float: left;
  font-weight: normal;
  font-size: 1rem;
}
section:nth-child(7) p:nth-child(3) i {
  margin-right: 10px;
  left: 0;
  font-size: 0.9rem;
}

/* employees list */

.employees_list {
  background-color: var(--color-accent);
  margin-top: 100px;
}
.employees_list .card {
  background-color: transparent;
  text-align: left;
  float: left;
  border: 0.3px dashed var(--primary-color);
}
.employees_list .card {
  margin: 80px auto;
}
.user_info {
  /* background-color: violet; */
  display: flex;
  align-items: center;
}
.employees_list .card div:nth-child(2) {
  /* background-color: violet; */
  font-size: 1rem;
  margin-top: -30px;
}

/* footer */
.sticky-footer {
  background-color: var(--color-accent);
  position: sticky;
  top: 100%;
}
footer .col:nth-child(5) {
  margin-top: -30px;
}
footer h5 {
  font-size: 1.2rem !important;
}
.fa-clock {
  font-size: 0.93rem !important;
}
footer .col:first-child a:first-child img {
  align-self: center;
  height: 4rem;
  margin-bottom: 30px;
}
footer .col:nth-child(2) h5,
footer .col:nth-child(3) h5 {
  margin-bottom: 20px;
}
footer .col:nth-child(2),
footer .col:nth-child(3) {
  line-height: 1.6;
}

footer .col:first-child i {
  font-size: 1.4rem;
  margin: 15px 10px;
}

/* login register */

.login-container {
  padding: 400px;
  margin-top: -400px;
  max-height: 50%;
  font-size: 0.8rem;
}

.login-container .btn.btn-danger {
  background-color: var(--secondary-color);
}
.login-container .btn.btn-primary {
  background-color: var(--primary-color);
}
/* Register */

/* reset passwrod */

@media screen and (max-width: 786px) {
  .employees_list .card div:nth-child(2) {
    justify-content: center;
  }
  section .about-main .row {
    /* background: chartreuse; */
    flex-direction: column;
  }
  section .about-main div:nth-child(2) {
    margin-top: 50px;
  }
  section .card {
    margin-bottom: 10px;
  }
  section .offers .row {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
  }

  .newsletter h2 {
    font-size: 5vw !important;
    margin-top: -25px;
    margin-bottom: 10px;
  }
  .newsletter p {
    font-size: 3vw;
    margin-bottom: -14px;
  }
  .content div:first-child {
    margin-top: 10%;
  }
  .content div:first-child h1 {
    font-size: 9vw !important;
    /* background-color: darkcyan; */
  }
  section .about div a {
    /* background-color: darkgoldenrod !important; */
    width: 150px !important;
  }
  .row {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
  }
  .navbar ul li {
    margin-bottom: 10px;
  }
  .navbar ul li ul {
    box-shadow: none;
  }
  .navbar ul li a i {
    float: right;
  }
  .newsletter {
    display: flex;
    flex-direction: column;
  }
  .newsletter button {
    display: flex;
    height: 90% !important;
    justify-content: center;
    width: 100%;
  }
  footer img {
    width: 50% !important;
  }
  footer .row {
    display: flex;
    flex-direction: column;
  }
  footer .container div div:first-child ul {
    justify-content: center;
  }
  footer .container div:nth-child(2) ul,
  footer .container div:nth-child(3) ul {
    align-items: center;
    display: flex;
    flex-direction: column;
    align-content: center;
  }
  footer .container .col:last-child {
    margin-top: 20px;
  }
}
